<template>
  <div>
    <LayoutBox one="APP管理">
      <div class="title">
        <h4>安卓</h4>
        <el-button v-if="!isAndroidUpdate" type="primary" @click="androidUpdate">修改android</el-button>
        <el-button v-else type="primary" @click="androidUpdate">保存android</el-button>
      </div>
      <el-form ref="androidForm" :model="androidForm" :rules="androidRules" label-width="80px">
        <el-form-item label="版本号" prop="appVersion">
          <el-input v-if="isAndroidUpdate" v-model="androidForm.appVersion" placeholder="请输入内容"></el-input>
          <span v-else>{{ androidForm.appVersion }}</span>
        </el-form-item>
        <el-form-item label="下载地址" prop="appUrl">
          <!-- <el-input  v-model="androidForm.appUrl" placeholder="请输入内容"></el-input> -->
          <el-upload v-if="isAndroidUpdate" class="upload-demo" action="http://39.100.129.155:8090/common/oss/uploadFile/" :limit="1" :on-success="onUploadSuccess">
            <el-button size="small" type="primary">点击上传</el-button>
          </el-upload>
          <span v-else>{{ androidForm.appUrl }}</span>
        </el-form-item>
        <el-form-item label="版本描述" prop="appDescribe">
          <el-input v-if="isAndroidUpdate" v-model="androidForm.appDescribe" placeholder="请输入内容"></el-input>
          <span v-else>{{ androidForm.appDescribe }}</span>
        </el-form-item>
      </el-form>
      <div class="title">
        <h4>ios</h4>
        <el-button v-if="!isIosUpdate" type="primary" @click="iosUpdate">修改ios</el-button>
        <el-button v-else type="primary" @click="iosUpdate">保存ios</el-button>
      </div>
      <el-form ref="iosForm" :model="iosForm" :rules="iosRules" label-width="80px">
        <el-form-item label="版本号" prop="appVersion">
          <el-input v-if="isIosUpdate" v-model="iosForm.appVersion" placeholder="请输入内容"></el-input>
          <span v-else>{{ iosForm.appVersion }}</span>
        </el-form-item>
        <el-form-item label="下载地址" prop="appUrl">
          <el-input v-if="isIosUpdate" v-model="iosForm.appUrl" placeholder="请输入内容"></el-input>
          <span v-else>{{ iosForm.appUrl }}</span>
        </el-form-item>
        <el-form-item label="版本描述" prop="appDescribe">
          <el-input v-if="isIosUpdate" v-model="iosForm.appDescribe" placeholder="请输入内容"></el-input>
          <span v-else>{{ iosForm.appDescribe }}</span>
        </el-form-item>
      </el-form>
    </LayoutBox>
  </div>
</template>

<script>
import LayoutBox from '@/components/LayoutBox/index'
export default {
  name: 'app',

  components: {
    LayoutBox
  },

  data () {
    return {
      isAndroidUpdate: false, // 安卓是否开启编辑
      isIosUpdate: false, // ios是否开启编辑
      androidForm: {}, // 安卓表单提交
      androidRules: {
        appVersion: [
          { required: true, message: '请输入版本号', trigger: 'blur' }
        ],
        appUrl: [
          { required: true, message: '请输入下载地址', trigger: 'blur' }
        ],
        appDescribe: [
          { required: true, message: '请输入版本描述', trigger: 'blur' }
        ],
      },
      iosForm: {}, // ios表单提交
      iosRules: {
        appVersion: [
          { required: true, message: '请输入版本号', trigger: 'blur' }
        ],
        appUrl: [
          { required: true, message: '请输入下载地址', trigger: 'blur' }
        ],
        appDescribe: [
          { required: true, message: '请输入版本描述', trigger: 'blur' }
        ],
      },
    }
  },

  methods: {
    // 点击安卓的修改
    androidUpdate () {
      if (this.isAndroidUpdate) {
        let formRules = this._formRules('androidForm')
        if (formRules) {
          formRules.appType = 0
          this.apiAppVersion(formRules)
            .then((res) => {
              if (res.data.success) {
                this.isAndroidUpdate = !this.isAndroidUpdate
              } else {
                this.$message.error(res.data.msg)
              }
            })
        }
      } else {
        this.isAndroidUpdate = !this.isAndroidUpdate
      }
    },
    // 点击ios的修改
    iosUpdate () {
      if (this.isIosUpdate) {
        let formRules = this._formRules('iosForm')
        if (formRules) {
          formRules.appType = 1
          this.apiAppVersion(formRules)
            .then((res) => {
              if (res.data.success) {
                this.isIosUpdate = !this.isIosUpdate
              } else {
                this.$message.error(res.data.msg)
              }
            })
        }
      } else {
        this.isIosUpdate = !this.isIosUpdate
      }
    },
    // 上传文件成功
    onUploadSuccess (response) {
      this.androidForm.appUrl = response.data.webUrl
    },

    // 私有方法------------------------------------------------------
    // 表单验证
    _formRules (formName) {
      let data = false
      this.$refs[formName].validate((valid) => {
        if (valid) {
          data = this[formName]
        } else {
          console.log('error submit!!')
          data = false
          return
        }
      })
      return data
    },

    // api-----------------------------------------------
    // 获取当前启用版本
    apiGetActivity () {
      this.$http.get(`/woho/appVersion/getActivity/0`)
        .then((res) => {
          if (res.data.success) {
            this.androidForm = res.data.data
          } else {
            this.$message.error(res.data.msg)
          }
        })
      this.$http.get(`/woho/appVersion/getActivity/1`)
        .then((res) => {
          console.log(res.data.data)
          if (res.data.success) {
            this.iosForm = res.data.data
          } else {
            this.$message.error(res.data.msg)
          }
        })
    },
    // 添加版本
    apiAppVersion (data) {
      return this.$http.post('/woho/appVersion/add', data).then((res) => res)
    }
  },

  created () {
    this.apiGetActivity()
  }
}
</script>

<style lang="scss" scoped>
  .title{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 200px;
  }
</style>
